<template><section><h1>Radio 单选框</h1>
<hr>
<p>在一组备选项中进行单选</p>
<h3>基础用法</h3>
<p>由于选项默认可见，不宜过多，若选项过多，建议使用 Select 选择器。</p>
<div class="demo-block">
  <v-radio v-model="radio" label="1">备选项</v-radio>
  <v-radio v-model="radio" label="2">备选项</v-radio>
  
</div>
<demo-block>
                        <div slot="desc"><v-radio v-model="radio" label="1">备选项</v-radio>
<v-radio v-model="radio" label="2">备选项</v-radio>


</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">radio</span>: <span class="hljs-string">'1'</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<p>单选框不可用的状态。</p>
<div class="demo-block">
  <v-radio v-model="radio" label="1" disabled>备选项</v-radio>
  <v-radio v-model="radio" label="2" disabled>备选项</v-radio>
  
</div>
<demo-block>
                        <div slot="desc"><v-radio v-model="radio" label="1" disabled>备选项</v-radio>
