      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">radio</span>: <span class="hljs-string">'1'</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>单选框组</h3>
<p>适用于在多个互斥的选项中选择的场景</p>
<div class="demo-block">
  <v-radio-group v-model="radio">
   <v-radio label="1">备选项</v-radio>
   <v-radio label="2">备选项</v-radio>
   <v-radio label="3">备选项</v-radio>
  </v-radio-group>
  
</div>
<demo-block>
                        <div slot="desc"><v-radio-group v-model="radio">
  <v-radio label="1">备选项</v-radio>
  <v-radio label="2">备选项</v-radio>
  <v-radio label="3">备选项</v-radio>
</v-radio-group>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-radio-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'radio'</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span> &gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span> &gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"3"</span> &gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">radio</span>: <span class="hljs-string">'1'</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    data () {
      return {
        radio: '1'
      }
    }
  }
</script>


